@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
  
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #1b1523;
  color: #fff;
}

.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
button:hover {
  background: #c23866;
  color: #fff;
}

.card__grid{
  display: grid;
  grid-gap: 30px;
  gap: 30px;
  grid-template-columns: repeat(4,1fr);
  margin-top: 40px;
}

.turns{
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  display: block;
}
.single__card img{border:2px solid #fff;border-radius:6px}.front{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);position:absolute;transition:all .2s ease-in-out}.back{transition:all .2s ease-in-out;transition-delay:.2s}.flipped .front{-webkit-transform:rotateY(0deg);transform:rotateY(0deg);transition-delay:.2s}.flipped .back{-webkit-transform:rotateY(90deg);transform:rotateY(90deg);transition-delay:0s}
